import React, { forwardRef } from 'react';
import { Link } from 'react-router-dom';
import { FaClipboardList, FaHeart, FaUserCircle, FaSignInAlt } from 'react-icons/fa';

const ShoppingBagDropdown = forwardRef(({ isOpen }, ref) => {
  if (!isOpen) return null;

  return (
    <div className="shopping-bag-dropdown-overlay" ref={ref}>
      <div className="shopping-bag-dropdown-content">
        <h3></h3>
        <p className="view-recent">
          <Link to="/login" className="login-link"></Link>
          <span className="view-recent-text"></span>
        </p>
        <p className="account-title"></p>
        <ul className="account-links">
          <li><FaClipboardList /><Link to="/orders"></Link></li>
          <li><FaHeart /><Link to="/favorites"></Link></li>
          <li><FaUserCircle /><Link to="/account"></Link></li>
          <li><FaSignInAlt /><Link to="/login"></Link></li>
        </ul>
      </div>
    </div>
  );
});

export default ShoppingBagDropdown;