import React from 'react';
import logo from '../../assets/logos/zhoukunlinLogoWhiteCharacter.svg';

function Logo() {
  return (
    <a href="https://zapps.pro" className="logo" style={{ 
      width: 'auto', 
      height: '44px', 
      display: 'flex', 
      alignItems: 'center',
      justifyContent: 'flex-start'
    }}>
      <img 
        src={logo} 
        alt=" Logo" 
        style={{ 
          maxWidth: '75%', 
          maxHeight: '80%', 
          objectFit: 'contain'
        }}
        onError={(e) => {
          console.error('Logo', e);
          e.target.style.display = 'none';
        }}
      />
    </a>
  );
}

export default Logo;
